<template>
    <div>
        <b-modal id="deactivePartner" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">Delivery Partner</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="partnerStatus == 1" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you sure</h3>
                    <p v-if="partnerStatus == 1" class="mb-5">you want to activate this partner?</p>
                    <p v-else class="mb-5">you want to deactivate this partner?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactivePartner')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activeDeactiveStatus();">Ok</button>
                </div>
            </div>
        </b-modal>
        <div class="d-block header-section">
            <div class="py-3 px-3 align-self-center d-sm-flex w-100 rounded">
                <div class="w-sm-100 mr-auto">
                    <h1>
                        <b>Delivery Partner</b>
                    </h1>
                </div>
                <div class="float-right"> <button type="button" class="btn btn-link" v-on:click="loadnewShipper()"><img src="@/assets/images/icons/add.png" alt=""> NEW PARTNER</button></div>
            </div>
        </div>
        <div class="container-fluid productManagement">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12">
                    <div class="card ">
                        <div class="card-header p-0">
                            <b-collapse id="shipper" v-model="showNewShipper">
                                <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Delivery Partner Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.name.$error }">
                                                    <input type="text" class="form-control" placeholder="Delivery Partner Name" v-model="newShipper.name">
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.name.required">Delivery Partner Name is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.name.checkDuplicate">Delivery Partner Name already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Registration Number</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.RegNumber.$error }">
                                                    <input type="text" class="form-control" placeholder="Registration Number" v-model="newShipper.RegNumber">
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.RegNumber.alphaNum">Special characters are not allowed</span>
                                                        <span class="text-danger" v-if="!$v.newShipper.RegNumber.required">Registration Number is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.RegNumber.checkDuplicate">Registration Number already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.contactName.$error }">
                                                    <input type="text" class="form-control" placeholder="Contact Name" v-model="newShipper.contactName">
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.contactName.required">Contact Name is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Email Id</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.email.$error }">
                                                    <input type="text" class="form-control" placeholder="Email Id" v-model="newShipper.email" :disabled="newShipper.id!=0">
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.email.required">Email Id is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.email.email">Emai id is not valid</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.email.checkEmailDuplicate">Emai id already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Number</label>
                                            <div class="col-sm-8">
                                                <div class="input-group mb-3">
                                                    <div class="form-group col-sm-4 p-0" :class="{ 'form-group--error': $v.newShipper.countryCode.$error }">
                                                        <input type="text" class="form-control " placeholder="+233" v-model="newShipper.countryCode">
                                                        <div v-if="addShipperErrors">
                                                            <span class="text-danger" v-if="!$v.newShipper.countryCode.required">Country code is required</span>
                                                        </div>
                                                    </div>
                                                    <div class="form-group col-sm-8 pr-0" :class="{ 'form-group--error': $v.newShipper.phone.$error }">
                                                        <input type="text" class="form-control" v-model="newShipper.phone" placeholder="Contact Number">
                                                        <p v-if="addShipperErrors"> <span class="text-danger" v-if="!$v.newShipper.phone.required">Contact Number is required</span></p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Country</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.country.$error }">
                                                    <select class="form-control" v-model="newShipper.country" v-on:change="changeCountry()">
                                                        <option value=""> Select Country</option>
                                                        <option :key="index" v-for="(country,index) in $store.getters.countries" :value="country.name">{{country.name}}</option>
                                                    </select>
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.country.required">Country is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.country.checkEmpty">Country is not valid</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Region</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.region.$error }">
                                                    <select class="form-control" v-model="newShipper.region">
                                                        <option value=""> Select Region</option>
                                                        <option :key="index" v-for="(region,index) in regions" :value="region.name">{{region.name}}</option>
                                                    </select>
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.region.required">State is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newShipper.region.checkEmpty">State is not valid</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.address.$error }">
                                                    <textarea class="form-control" placeholder="Address" v-model="newShipper.address"></textarea>
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.address.required">Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Postal Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newShipper.postalCode.$error }">
                                                    <input type="text" class="form-control" placeholder="Postal Address" v-model="newShipper.postalCode">
                                                    <div v-if="addShipperErrors">
                                                        <span class="text-danger" v-if="!$v.newShipper.postalCode.required">Postal Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button class="btn btn-primary float-right mb-4 pl-5 pr-5 p-2 " name="save" value="SAVE" type="button" v-on:click="addUpdateShipper()">Submit</button>
                                    <button class="btn btn-outline-primary float-right mb-4 pl-5 pr-5 mr-3 p-2" name="save" value="CANCEL" type="button" v-on:click="clearData()">CANCEL</button>
                                </div>

                                <div class="clearfix"></div>
                            </b-collapse>
                            <div class="list-filter">
                                <div class="btn-group ml-0">
                                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="ALL" checked :value="allShippersCount">
                                    <label type="button" class="btn btn-outline-dark rounded-btn mr-2 mob-mar" for="ALL" @click="bindShippers('ALL')">ALL({{allShippersCount}})</label>
                                </div>
                                <div class="btn-group">
                                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Active" :value="activeShippersCount">
                                    <label type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" for="Active" @click="bindShippers('Active')">Active({{activeShippersCount}})</label>
                                </div>
                                <div class="btn-group">
                                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="InActive" :value="inActiveShippersCount">
                                    <label type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" for="InActive" @click="bindShippers('InActive')">Inactive({{inActiveShippersCount}})</label>
                                </div>
                            </div>
                            <div class="col-12 col-sm-12 p-0">
                                <div class="table-responsive">
                                    <table class="table bordered" v-if="getShippers.length>0">
                                        <thead>
                                            <tr>
                                                <th class="p-3">Name</th>
                                                <th class="p-3">Registration Number</th>
                                                <th class="p-3">Registration Date</th>
                                                <th class="p-3">Status</th>
                                                <th class="p-3">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(shipper,index) in getShippers" :key="index">
                                                <td class="p-2">{{shipper.name}}</td>
                                                <td class="p-2">{{shipper.regNmbr}}</td>
                                                <td class="p-2">{{shipper.regDate | FormatingDate}}</td>
                                                <td v-if="shipper.status==1" class="text-success p-2">Active</td>
                                                <td v-else class="text-danger p-2">Inactive</td>
                                                <td class="p-2">
                                                    <a v-on:click="editShipper(shipper.id)" class="text-primary"> <img src="@/assets/images/icons/edit-gray.png" /> Edit </a>
                                                    <a v-if="shipper.status==1" class="text-danger" v-on:click="getActiveDeactiveStatus(shipper.id,2)"><img src="img/icons/deactivate-new.png " /> Deactivate</a>
                                                    <a v-else class="text-success" v-on:click="getActiveDeactiveStatus(shipper.id,1)"><img src="img/icons/deactivate-new.png " /> Activate</a>
                                                    <router-link :to="{ name: 'Users',params: { shipperId:shipper.id,shipperName:shipper.name} }">
                                                        <img src="@/assets/images/icons/edit-gray.png" /> Manage Users
                                                    </router-link>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <nav aria-label="Page navigation example">
                                <ul class="pagination float-right mr-5 mb-5 mt-4">
                                    <!-- <li class="page-item"><a class="page-link" href="#">Prev</a></li>
                                    <li class="page-item active"><a class="page-link" href="#">1</a></li>
                                    <li class="page-item"><a class="page-link" href="#">2</a></li>
                                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                                    <li class="page-item"><a class="page-link" href="#">Next</a></li> -->
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>
<script>
    import { required,email, alphaNum } from 'vuelidate/lib/validators';
    import { checkNullOrEmpty, checkEmailDuplicate } from '@/Validations';
    import toast from '@/toast';
    export default {
        data() {
            return {
                newShipper: this.assignShipperFields(), allShippersCount: 0, activeShippersCount: 0,
                inActiveShippersCount: 0, addShipperErrors: false, shippersList: [],regions:[],showNewShipper:false, 
                shipperId:'', partnerStatus:1
            };
        },
        async mounted() {
            await this.$store.dispatch("getCountriesAsync");
            await this.$store.dispatch("getShippersAsync");
            this.bindShippers('ALL');
        },
        computed: {
            getShippers() {
                return this.shippersList;
            }
        },
        validations: {
            newShipper: {
                name: {
                    required,
                    checkDuplicate(value) {
                        if (this.$store.getters.shippers.filter(element => element.name.toLowerCase() === value.toLowerCase()).length > 0) {
                            if (this.newShipper.id !== 0) return true; return false;
                        }
                        return true;
                    }
                },
                RegNumber: {
                    required,alphaNum,
                    checkDuplicate(value) {
                        if (this.$store.getters.shippers.filter(element => element.registerationNumber.toLowerCase() === value.toLowerCase()).length > 0) {
                            if (this.newShipper.id !== 0) return true; return false;
                        }
                        return true;
                    }
                },
                contactName: { required },
                email: {
                    required,email,
                    checkEmailDuplicate(value) {
                            if (this.newShipper.id !== 0) return true;
                            return checkEmailDuplicate(value);
                    }
                },
                phone: { required },
                address: { required },
                postalCode: { required },
                countryCode:{required},
                country: {required,checkNullOrEmpty},
                region: {required,checkNullOrEmpty}
            }
        },
        filters: {
          FormatingDate: function (value) {
              if(value==null || value=='') return '';
              var date=new Date(value);
              var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
                return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()]  + "-" + date.getFullYear();
            }
        },
        components: {},
        methods: {
            assignShipperFields() {
                var obj = { id: 0, name: '', RegNumber: '', contactId: 0, contactName: '', countryCode: '+91', email: '', phone: '', country: '',region: '', addressId: 0, address: '',postalCode:'' };
                return obj;
            },
            changeCountry(){
                var regions=this.$store.getters.countries.filter(element=>element.name===this.newShipper.country).map(element=>element.regions);
                this.regions=regions[0];
            },
            loadnewShipper() {
                this.newShipper = this.assignShipperFields(); this.addShipperErrors=false;
                this.showNewShipper=true;
            },
            async addUpdateShipper() {
                this.$v.$touch();
                this.addShipperErrors = this.$v.newShipper.$anyError;
                if (!this.addShipperErrors) {
                    var country = this.$store.getters.countries.filter(element => element.name === this.newShipper.country);
                    var countryId=country[0].id;
                    var regionId = country[0].regions.filter(element => element.name === this.newShipper.region).map(element => element.id);
                    var objShipper = {
                        id: this.newShipper.id, name: this.newShipper.name, registerationNumber: this.newShipper.RegNumber,
                        status: 1, contact: {
                            email: this.newShipper.email, id: this.newShipper.contactId,
                            name: this.newShipper.contactName, countryCode: this.newShipper.countryCode, phone: this.newShipper.phone
                        },
                        address: {
                            id: this.newShipper.addressId, description: this.newShipper.address,postalAddress:this.newShipper.postalCode,
                            country: { id: parseInt(countryId), name: this.newShipper.country, regions: null },
                            region:{id:parseInt(regionId),name:this.newShipper.region,countryId:0},
                        }
                    };
                    this.newShipper = this.assignShipperFields();
                    this.showNewShipper=false;
                    await this.$store.dispatch("addShipperAsync", objShipper);
                    await toast.success("Delivery Partner Added!..");
                    await this.bindShippers('ALL');
                }
            },
            editShipper(shipperId) {
                var obj = this.$store.getters.shippers.filter(element => element.id === shipperId);
                var currentShipper = obj[0];
                this.newShipper = {
                    id: currentShipper.id, name: currentShipper.name,postalCode:currentShipper.address.postalAddress,
                    RegNumber: currentShipper.registerationNumber, contactId: currentShipper.contact.id, contactName: currentShipper.contact.name,
                    email: currentShipper.contact.email, phone: currentShipper.contact.phone, countryCode: currentShipper.contact.countryCode,
                    country: currentShipper.address.country.name,region:currentShipper.address.region.name, addressId: currentShipper.address.id, address: currentShipper.address.description
                };
                this.changeCountry();
                this.showNewShipper=true;
            },
            async activeDeactiveStatus() {
                await this.$store.dispatch("activateDeactivateShipperAsync", { orgId: this.shipperId, orgStatus: this.partnerStatus });
                this.$root.$emit('bv::hide::modal', 'deactivePartner');
                await this.$store.dispatch("getShippersAsync");
                await this.bindShippers('ALL');
            },
            getActiveDeactiveStatus(id, status){
                this.shipperId = id;
                this.partnerStatus = status;
                this.$root.$emit('bv::show::modal', 'deactivePartner');
            },
            clearData() {
                this.newShipper = this.assignShipperFields();
                this.showNewShipper=false;
            },
            bindShippers(filterStatus) {
                var shippers = []; this.shippersList = [],
                this.allShippersCount=this.$store.getters.shippers.length;
                this.activeShippersCount=this.$store.getters.activeShippers.length;
                this.inActiveShippersCount=this.$store.getters.inActiveShippers.length;
                switch (filterStatus) {
                    case 'ALL': {
                        this.$store.getters.shippers.forEach(element => { shippers.push(element) });
                        break;
                    }
                    case 'Active': {
                        this.$store.getters.activeShippers.forEach(element => { shippers.push(element) });
                        break;
                    }
                    case 'InActive': {
                        this.$store.getters.inActiveShippers.forEach(element => { shippers.push(element) });
                        break;
                    }
                }
                shippers.forEach(element => {
                    var objShipper = { id: element.id, name: element.name, regNmbr: element.registerationNumber, regDate: element.registerationDate, status: element.status };
                    this.shippersList.push(objShipper);
                });
            }
        }
    }
</script>